import Vue from 'vue'
import VueRouter from 'vue-router'
import hello from '../views/hello.vue'
import Login from '../views/login.vue'
import Head from '../views/head.vue'
Vue.use(VueRouter)

const routes = [
  
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta:{
      // 页面标题title
      title: '首页'
  }
  },
  {
    path: '/hello',
    name: 'hello',
    component: hello,
    meta:{
      // 页面标题title
      title: '第二页'
  }
  },
  {
    path: '/head',
    name: 'head',
    component: Head,
    meta:{
      // 页面标题title
      title: '第3页'
  }
  },
  {
    path: "/", redirect: { name: "login" },
   
  },
  {
    path: "/1", redirect: { name: "hello" }
  },
]




const router = new VueRouter({
  mode:'history',//开启history模式需要服务器支持
  base: '/admin/',// 配置单页应用的基路径,我的理解就是访问这个地址的时候开始是/，根据路由时/跳转到登录页面
  routes
})



export default router;
