<template>
  <div class="first">
    <div class="second">
      <div class="third">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="用户名" class="long1">
            <el-input v-model="form.userName" style="margin: auto"></el-input>
          </el-form-item>
          <el-form-item label="密码" class="long1">
            <el-input
              v-model="form.password"
              type="password"
              style="margin: auto"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item class="long2">
            <el-button type="primary" @click="submitForm">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  metaInfo: {
    title: "登录页面",
  },
  data() {
    return {
      form: {
        userName: "cj",
        password: "1234",
      },
    };
  },
  methods: {
    submitForm() {
      // axios
      //   .get("/hello/2")
      //   .then((response) => {
      //     // handle success
      //     console.log(response.data);
      //   })
      //   .catch((error) => {
      //     // handle error
      //     console.log(error);
      //   });

      axios
        .post("http://localhost:8077/user/login", this.form)
        .then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: "登录成功",
              type: "success",
            });

            this.$router.push("/hello");
          }

          console.log(res);
        })
        .catch((err) => {
          //请求出错
          alert("账号或密码错误");
          console.log(err);
        });
    },
  },
};
</script>
<style scoped>
html,
body {
  width: 100%;
  height: 100%;
}
body {
  background-image: linear-gradient(to bottom right, #7a88ff, #7affaf);
}
.second {
  opacity: 0.9;
  width: 100%;
  height: 100%;
  position: fixed;
  background-image: linear-gradient(to bottom, #7a88ff, #7affaf);
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-size: 100% 100%;
}
.third {
  margin-top: 200px;
}
/* 设置输入框样式 */
.el-input.el-input__inner {
  background-color: rgba(255, 255, 255, 0.247);
}
.long1 {
  width: 500px;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
}
.long2 {
  width: 200px;
  margin: auto;
  margin-top: 30px;
}
</style>
